<template><section><html><head></head><body><h1>BillboardGraphics</h1>
<p><code v-pre="">vc-graphics-billboard</code> 组件用于加载布告板（二维图标），需要作为 <code v-pre="">vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载布告板</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity" @mouseover="mouseover" @mouseout="mouseout" :position="position" :description="description" :id="id" :billboard.sync="billboard">
          <vc-graphics-billboard :image="image" :scale="scale" :show="show" :distance-display-condition="distanceDisplayCondition" :horizontal-origin="horizontalOrigin"></vc-graphics-billboard>
        </vc-entity>
      </vc-viewer>
      <div class="demo-tool">
        <span>{{ show ? '隐藏' : '显示' }}</span>
        <md-switch v-model="show"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity"</span>
        <span class="hljs-attr">@mouseover</span>=<span class="hljs-string">"mouseover"</span>
        <span class="hljs-attr">@mouseout</span>=<span class="hljs-string">"mouseout"</span>
        <span class="hljs-attr">:position</span>=<span class="hljs-string">"position"</span>
        <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span>
        <span class="hljs-attr">:id</span>=<span class="hljs-string">"id"</span>
        <span class="hljs-attr">:billboard.sync</span>=<span class="hljs-string">"billboard"</span>
      &gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-billboard</span>
          <span class="hljs-attr">:image</span>=<span class="hljs-string">"image"</span>
          <span class="hljs-attr">:scale</span>=<span class="hljs-string">"scale"</span>
          <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>
          <span class="hljs-attr">:distance-display-condition</span>=<span class="hljs-string">"distanceDisplayCondition"</span>
          <span class="hljs-attr">:horizontal-origin</span>=<span class="hljs-string">"horizontalOrigin"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-billboard</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tool"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{ show ? '隐藏' : '显示' }}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"show"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">id</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">description</span>: <span class="hljs-string">'This is a billboard'</span>,
        <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>,
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">90</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> },
        <span class="hljs-attr">billboard</span>: {},
        <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">distanceDisplayCondition</span>: { <span class="hljs-attr">near</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">far</span>: <span class="hljs-number">20000000</span> },
        <span class="hljs-attr">horizontalOrigin</span>: <span class="hljs-number">0</span>,
        <span class="hljs-attr">scale</span>: <span class="hljs-number">0.25</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
