<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 billboard 是否显示。</td>
</tr>
<tr>
<td>image</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 加载的的 Image、 URI 或者 Canvas。</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 图片的缩放比例。</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0}</code></td>
<td><code v-pre="">optional</code> 指定 billboard 像素偏移。<strong>结构：{ x: number, y: number }</strong></td>
</tr>
<tr>
<td>eyeOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> 指定 billboard 视角偏移。<strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>horizontalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 水平对齐方式。<strong>CENTER: 0, LEFT: 1, RIGHT: -1</strong></td>
</tr>
<tr>
<td>verticalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 垂直对齐方式。 <strong>CENTER: 0, BOTTOM: 1, BASELINE: 2, TOP: -1</strong></td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> 指定 billboard 图片的颜色。</td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 沿 x 轴方向旋转的角度。</td>
</tr>
<tr>
<td>alignedAxis</td>
<td>Cartesian3</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> 指定 billboard 按单位矢量轴旋转参数。<strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>sizeInMeters</td>
<td>Boolean</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的单位是否是米。</td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的宽度（像素）。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的高度（像素）。</td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 随相机距离缩放的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 随相机距离透明度改变的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>pixelOffsetScaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 随相机距离像素偏移改变的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>imageSubRegion</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的子区域，相对于左下角。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 随相机距离改变是否显示参数。<strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 深度检测距离。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BillboardGraphics.html">BillboardGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          id: 'Hello Vue Cesium',
          description: 'This is a billboard',
          image: 'https://zouyaoji.top/vue-cesium/favicon.png',
          position: { lng: 90, lat: 40, height: 10000 },
          billboard: {},
          show: true,
          distanceDisplayCondition: { near: 0, far: 20000000 },
          horizontalOrigin: 0,
          scale: 0.25
        }
      },
      mounted() {
        this.$refs.entity.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          viewer.zoomTo(cesiumObject)
        })
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
        },
        mouseover() {
          this.scale = 0.5
        },
        mouseout() {
          this.scale = 0.25
        }
      }
    }
  </script>